html {
  box-sizing: border-box;
  height: 100%;
}
*, *::after, *::before {
  box-sizing: inherit;
}

body {
  margin: 0;
  height: 100%;
  padding: 8px;
}

.MuiTable-root .MuiTableHead-root .MuiTableCell-root {
  padding: 2px 8px;
}
.MuiTable-root .MuiTableCell-root:first-child {
  padding-left: 8px;
  border-left: 0;
}
.MuiTable-root .MuiTableCell-root:last-child {
  padding-right: 8px;
  border-right: 0;
}

.MuiTable-root .MuiTableCell-root {
  padding: 8px;
}
.datepicker-container {
  width: 240px;
}
.datepicker-container .MuiOutlinedInput-input {
  padding: 9px 14px;
}